%headings {
    font-family: $headings-font-family;
    font-weight: $headings-font-weight;
    line-height: $headings-line-height;
    letter-tracking: -25;
    margin-top: 0;
    margin-bottom: $headings-margin-bottom;

    &:last-child {
        margin-bottom: 0;
    }
    small,
    .small {
        letter-tracking: -50;
        font-family: $font-family-base;
        font-weight: $font-weight-bold;
        text-transform: none;
        font-size: 75%;
    }
}

%heading1 {
    @include serif-text-crop;

    @include fs(3.5, 5);
}

%heading2 {
    @include serif-text-crop;

    @include fs(3, 4);
}

%heading3 {
    @include sans-text-crop($headings-line-height);

    @include fs(2.5, 3);

    letter-tracking: -50;
    font-family: $font-family-base;
    font-weight: $font-weight-bold;
}

%heading4 {
    @include serif-text-crop;

    font-size: $h4-font-size;
}

%heading5 {
    @include fs(1.5, 1);
    @include sans-text-crop($headings-line-height);

    letter-tracking: -50;
    font-family: $font-family-base;
    font-weight: $font-weight-bold;
}

%heading6 {
    @include sans-text-crop($headings-line-height);

    font-family: $font-family-base;
    font-weight: $font-weight-bold;
    letter-tracking: 200;
    text-transform: uppercase;
    font-size: $h6-font-size;
}

%lead {
    font-size: $lead-font-size;
    font-weight: $lead-font-weight;
    letter-tracking: $letter-spacing;
}

// Type display classes
%display1 {
    @extend %heading1 !optional;

    @include serif-text-crop(1.1);

    @include fs(5, 7);
}

%display2 {
    @extend %heading2 !optional;

    @include serif-text-crop;

    @include fs(4, 6);
}

%display3 {
    @extend %heading3 !optional;

    @include sans-text-crop($headings-line-height);
    @include fs(3, 5);
}

%display4 {
    @extend %heading6 !optional;

    @include sans-text-crop($headings-line-height);
    @include fs(1.5, 3);
}

%smallnormal {
    font-size: ms(-0.5);
    line-height: 1.4;
    font-weight: $font-weight-normal;
    letter-spacing: 0;
    @include media-breakpoint-up($d-bp) {
        font-size: $font-size-sm;
    }
}

%smallbold {
    @extend %heading6;

    @include sans-text-crop($headings-line-height);

    font-size: $font-size-sm;
}
