// Modular Scale ==============================================================
@import 'mathsass/dist/_math';
@import 'modularscale-sass/stylesheets/modularscale';

@import 'build/sass/functions';
@import 'build/sass/variables';
@import 'build/sass/mixins';
@import 'build/sass/partials/bootstrap/utilities/type';
// Only put styles that apply to the Home page

#home-header {
    .section-inner {
        background-color: rgba(#000, 0.75);
    }
}

.menu-home-header-container {
    overflow: hidden;
}

#home-header-buttons {
    .content-item {
        height: 100%;
        display: flex;
        p {
            height: 100%;
            display: flex;
            .btn {
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}
.menu-home-header-container {
    overflow: hidden;
}
#menu-home-header {
    width: calc(100vw + 1px);
    margin: 0 0 0 -1px !important;
    border-top: $nav-tabs-border-width solid #fff;
    li.nav-item {
        border-color: #fff !important;
        border-right: $nav-tabs-border-width solid #fff;
        @include media-breakpoint-up(md) {
            width: 33.333%;
        }
        .nav-link {
            color: #fff;
            @include hover-focus-active {
                text-decoration: none;
                background-color: $primary;
                color: yiq($primary);
            }
            .nav-link-text {
                @extend %headings !optional;
                @extend %heading4 !optional;

                font-size: $h5-font-size;
            }
            .nav-link-icon {
                font-size: ms(6);
                color: $secondary;
                margin-right: ms(0);
            }
        }
    }
}
#expertise-award a {
    width: 65vw;
    max-width: ms(10);
    display: block;
    svg {
        display: block;
    }
}

#bar-logos {
    overflow: hidden;
    .row {
        padding: 1rem;
        @include media-breakpoint-up($d-bp) {
            padding: 1rem 0;
        }
    }
    .content-wrapper {
        width: calc(100% + #{$grid-gutter-width/2});
        margin: 0 -#{$grid-gutter-width/2} -#{$grid-gutter-width/2} 0;
    }
    .wpb_single_image.content-item {
        margin: 0 ($grid-gutter-width/2) ($grid-gutter-width/2) 0;
    }
    .wpb_single_image {
        width: 35vw;
        max-width: ms(8);
        figure,
        div,
        picture {
            display: block;
        }
    }
}

nav.practice-areas-nav {
    .nav {
        width: calc(100% + #{$grid-gutter-width});
        flex-direction: row;
        flex-wrap: wrap;
        margin-right: -#{$grid-gutter-width};
    }
    li.nav-item {
        padding-right: #{$grid-gutter-width};
        border: none;
        @include media-breakpoint-up(md) {
            flex: 0 0 50%;
            max-width: 50%;
        }
        a.nav-link {
            padding-left: 0;
            padding-right: 0;
            border-bottom: $border-width solid $border-color;
            @include hover-focus-active {
                color: #fff;
                text-decoration: none;
            }
        }
        .nav-link-text {
            @extend %headings !optional;
            @extend %heading4 !optional;

            font-size: $h5-font-size;
            &::before,
            &::after {
                display: none;
            }
        }
    }
}

#social-grid {
    .hover-box-title {
        @include serif-text-crop(1, 0.05em, 0.18em);
    }
    #recent-posts {
        display: flex;
        flex-direction: column;
        .container-fluid {
            flex: 1 1 auto;
            display: flex;
            flex-direction: column;
        }
        .row {
            flex: 1 1 auto;
        }
    }
    #social-links {
        .content-item {
            display: flex;
            flex-direction: column;
        }
    }
    .social-profiles {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        overflow: hidden;
    }
    .social-profiles-list.list-inline {
        flex: 1 1 auto;
        @include make-row;
        .social-profile-item {
            @include make-col-ready;
            @include make-col(3);
            @include media-breakpoint-up(ml) {
                @include make-col(6);

                height: calc(50% -#{$grid-gutter-width});
            }

            flex: 1 1 auto;
            margin-right: 0;
            .social-profile-link {
                border-radius: 0;
                flex: 1 1 auto;
                @include hover-focus-active {
                    .profile-icon {
                        transform: scale(1.2);
                    }
                }
            }
            .social-profile-icon {
                width: 100%;
            }
            .profile-icon {
                font-size: ms(4);
            }
        }
    }
}
#reviews {
    .maat-reviews {
        @include media-breakpoint-up($d-bp) {
            width: 75%;
            margin: 0 auto;
        }
    }
}

#featured-post {
    overflow: hidden;
    @include hover-focus-active {
        .blog-image {
            transform: translate3d(
                0,
                calc(-#{$hover-box-padding + 2} - 1rem),
                0
            );
        }
        .card-img-overlay {
            transform: translate3d(
                0,
                calc(-#{$hover-box-padding + 2} - 1rem),
                0
            );
        }
        .btn-text {
            transform: translate3d(0, 0, 0);
            opacity: 1;
        }
        .stretched-link {
            transform: translate3d(
                0,
                calc(#{$hover-box-padding + 2} + 1rem),
                0
            );
        }
    }
    .card {
        &::after {
            content: 'Read More \00bb';
            @extend %heading6 !optional;

            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            padding: $hover-box-padding;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }
    }
    .embed-responsive {
        &::before {
            @include media-breakpoint-up(md) {
                padding-bottom: 100%;
            }
            @include media-breakpoint-up(ml) {
                padding-bottom: 50%;
            }
        }
    }
    .bg-secondary .card-img-overlay {
        background-color: rgba($dark, 0.8);
        transition: $hover-box-transition;
        z-index: 2;
        position: relative;
        @include media-breakpoint-up(md) {
            position: absolute;
        }
    }
    .blog-image {
        transition: $hover-box-transition;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include media-breakpoint-up(md) {
            position: relative;
        }
    }
    h3 {
        padding-bottom: $card-spacer-y;
    }
    .blog-title,
    .blog-excerpt {
        transition: $hover-box-transition;
    }
    .blog-link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: $hover-box-padding;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
    .btn-text {
        @extend %heading6 !optional;

        display: block;
        text-transform: uppercase;
        font-size: 1rem;
        height: 1rem;
        opacity: 0;
        transition: $hover-box-transition;
        transform: translate3d(0, 3rem, 0);
    }
}
